.dashboard {
    max-width: 1200px;
    margin: 0 auto;
}

.dashboard-header {
    text-align: center;
    margin-bottom: 40px;
    padding: 20px 0;
}

.dashboard-header h1 {
    font-size: 36px;
    font-weight: 700;
    color: #333;
    margin-bottom: 10px;
}

.dashboard-header p {
    font-size: 18px;
    color: #666;
}

.dashboard-content {
    display: grid;
    gap: 30px;
}

.welcome-card {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
}

.welcome-card .card-title {
    color: white;
}

.user-info p {
    margin-bottom: 8px;
    font-size: 16px;
}

.features-overview {
    margin-top: 20px;
}

.gamification-summary {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    border: none;
}

.gamification-summary .card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.view-all-link {
    color: white;
    text-decoration: none;
    font-weight: 600;
    opacity: 0.9;
    transition: opacity 0.2s ease;
}

.view-all-link:hover {
    opacity: 1;
}

.gamification-grid {
    display: grid;
    gap: 20px;
    grid-template-columns: 1fr;
}

.gamification-stats {
    display: flex;
    justify-content: space-around;
    gap: 20px;
}

.gamification-stats .stat-item {
    text-align: center;
}

.gamification-stats .stat-number {
    display: block;
    font-size: 24px;
    font-weight: 700;
    line-height: 1;
    margin-bottom: 4px;
}

.gamification-stats .stat-label {
    font-size: 12px;
    opacity: 0.9;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

.dashboard-streaks,
.dashboard-badges {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    padding: 16px;
}

.features-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    gap: 20px;
    margin-top: 20px;
}

.feature-item {
    padding: 20px;
    background: #f8f9fa;
    border-radius: 8px;
    border-left: 4px solid #007bff;
}

.feature-item h3 {
    color: #333;
    margin-bottom: 10px;
    font-size: 18px;
    font-weight: 600;
}

.feature-item p {
    color: #666;
    line-height: 1.5;
}

.quick-actions {
    margin-top: 20px;
}

.actions-grid {
    display: flex;
    gap: 15px;
    flex-wrap: wrap;
    margin-top: 20px;
}

.actions-grid .btn {
    min-width: 150px;
}

.btn-corporate {
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    color: white;
    border: none;
    padding: 12px 20px;
    border-radius: 6px;
    text-decoration: none;
    font-weight: 600;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    display: inline-block;
    text-align: center;
}

.btn-corporate:hover {
    transform: translateY(-2px);
    box-shadow: 0 4px 12px rgba(102, 126, 234, 0.3);
    color: white;
    text-decoration: none;
}

/* Responsive Design */
@media (max-width: 768px) {
    .dashboard-header h1 {
        font-size: 28px;
    }

    .dashboard-header p {
        font-size: 16px;
    }

    .features-grid {
        grid-template-columns: 1fr;
    }

    .actions-grid {
        flex-direction: column;
    }

    .actions-grid .btn {
        min-width: auto;
        width: 100%;
    }
}
